.dboard-build .card{
    min-height: 25.26vw;
    border: 1px solid #707070;
    width: 20vw;
}

.dboard-build .card .project-name{
    margin-bottom: 2vw;
    color: #000000;
    font-size: 1.4vw;
    font-weight: 500;
    letter-spacing: 0.34px;
}

.startbuilding button{
    border: 1px solid #F74B42 !important;
    background-color: transparent !important;
    font-size: 1vw;
    border-radius: 1rem;
    color: #F74B42;
    padding: 0.2vw 1.2vw;
    cursor: pointer;
}

.submitbuild button{
    border: 1px solid #F74B42 !important;
    background-color: #F74B42 !important;
    font-size: 1vw;
    padding: 0.2vw 1.2vw;
    color: #FFFFFF;
    border-radius: 1rem;
    cursor: pointer;
}

.editsub button{
    color: #F74B42;
    border: none;
    background-color: transparent;
    font-size: 0.95vw;
}

.carousel.carousel-slider .control-arrow:hover{
    background: unset !important;
}

.buildcard .carousel .slide{
    background: transparent !important;
}

.buildcard .carousel  .carousel-status{
    display: none;
}

.buildcard .carousel .carousel-slider .control-arrow{
    padding: 1.4rem !important;
}

.buildcard .carousel .control-dots{
    margin: 0 !important;
    bottom: 3vw;
}

.buildcard .carousel .control-dots .dot{
    box-shadow: unset !important;
    background: #070000;
}

.buildcard .carousel .slider .animated{
    transition-duration: 1000ms !important;
    transition: all 12.35s ease-in-out !important;
}

.buildcard .carousel .control-prev.control-arrow:before{
    border-right: 10px solid #241f1f;
    margin: 0 4vw 0 0;
}

.buildcard .carousel .control-next.control-arrow:before{
    border-left: 8px solid #241f1f;
    margin: 0 0 0 4vw;
}

.buildcard .carousel.carousel-slider .control-arrow{
    margin: 0 2vh;
    display: none;
}

.buildcard .carousel .thumbs-wrapper{
    display: none;
}

.buildcardmobile{
    display: none;
}

.project-no{
    font-size: 1.4vw;
    color: #0A0E2A;
    font-weight: 500;
}

.deadline{
    font-size: 1vw;
}

.lock-msg{
    font-size: 1vw;
    letter-spacing: 0.27px;
    background-color: #F0F0F2;
    display: none;
    box-shadow: 0px 10px 16px #00000042;
    padding: 1vw 2vw;
    top: 35%;
    position: absolute;
    width: 90%;
    right: 5%;
}

.lock:hover .lock-msg{
    display: block;
}

