@media (max-width:1200px){
    .dboard-build .card{
        min-height: 49.16vw;
        padding: 9.5vw 3.5vw;
        border-radius: 3vw;
        width: 89.72vw;
        margin: auto;
    }
    .buildcard{
        display: none;
    }

    .buildcardmobile{
        display: block;
        padding: 1.7rem;
    }
    .buildcardmobile .carousel .slide{
        background: transparent !important;
    }
    
    .buildcardmobile .carousel  .carousel-status{
        display: none;
    }
    
    .buildcardmobile .carousel .carousel-slider .control-arrow{
        padding: 1.4rem !important;
    }
    
    .buildcardmobile .carousel .control-dots{
        margin: 0 !important;
    }
    
    .buildcardmobile .carousel .control-dots .dot{
        box-shadow: unset !important;
        background: #070000;
    }
    
    .buildcardmobile .carousel .slider .animated{
        transition-duration: 1000ms !important;
        transition: all 12.35s ease-in-out !important;
    }
    
    .buildcardmobile .carousel .control-prev.control-arrow:before{
        border-right: 10px solid #241f1f;
        margin: 0 4vw 0 0;
    }
    
    .buildcardmobile .carousel .control-next.control-arrow:before{
        border-left: 8px solid #241f1f;
        margin: 0 0 0 4vw;
    }
    
    .buildcardmobile .carousel.carousel-slider .control-arrow{
        margin: 0 2vh;
        display: none;
    }
    
    .buildcardmobile .carousel .thumbs-wrapper{
        display: none;
    }

    .buildcardmobile .project-no{
        font-size: 4vw;
        margin-bottom: 0.6rem;
        margin-top: 0.4rem;
    }
    .buildcardmobile .deadline{
        font-size: 3vw;
        font-weight: 500;
        margin-bottom: 0.9rem;
    }

    .buildcardmobile .projectname{
        font-size: 4.1vw;
        font-weight: 500;
        margin-bottom: 0.5rem;
    }
    .startbuilding button{
        font-size: 2.5vw !important;
        padding: 0.9vw 3.2vw;
        border-radius: 4vw;
        font-weight: 500;

    }
    .submitbuild button{
        font-size: 2.5vw !important;
        padding: 1vw 2.2vw;
        font-weight: 500;
        border-radius: 4vw;
    }
    .buildcardmobile .carousel .control-dots{
        position: absolute;
        left: -30%;
        bottom: -0.4vw;
        
    }
    .buildcardmobile .carousel .control-dots .dot{
        height: 5px;
        width: 5px;
        margin: 0 8px;
        border-radius: 50%;
    }

    .editsub button{
        font-size: 2.2vw;
    }

    .lock-msg{
        font-size: 3vw;
        letter-spacing: 0.27px;
        background-color: #F0F0F2;
        display: none;
        box-shadow: 0px 10px 16px #00000042;
        padding: 1vw 2vw;
        top: 25%;
        position: absolute;
        width: 50%;
        right: 25%;
    }

    .lock img{
        width: 21.2vw !important;
    }
}



