.instructor-main-center .class-card .class-material-details .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #CFCFCF;
    -webkit-transition: .4s;
    transition: .4s;
    border: calc(1*var(--vp)) solid #CFCFCF;
    background-color: #CFCFCF;
    border-radius: calc(1000*var(--vp));
}

.slider:before {
    position: absolute;
    content: "";
    height: calc(24*var(--vp));
    width: calc(24*var(--vp));
    left: calc(0*var(--vp));
    bottom: calc(-6*var(--vp));
    border: calc(1*var(--vp)) solid #646464;
    background-color: white;
    -webkit-transition: .4s;
    border-radius: calc(1000*var(--vp));
    transition: .4s;
}

input:checked + .slider{
    border: calc(1*var(--vp)) solid #F74B42;
    background-color: #FFFFFF;
}

.instructor-main-center .class-card input:checked + .slider:before {
    -webkit-transform: translateX(calc(27*var(--vp)));
    -ms-transform: translateX(calc(27*var(--vp)));
    transform: translateX(calc(27*var(--vp)));
}

.instructor-main-center .class-card.active input:checked + .slider:before {
    border: calc(1*var(--vp)) solid #F74B42;
    background-color: #F74B42;
}

.instructor-main-center .class-card.upcoming input:checked + .slider:before {
    border: calc(1*var(--vp)) solid #4320BF;
    background-color: #4320BF;
}

.instructor-main-center .class-card.completed input:checked + .slider:before {
    border: calc(1*var(--vp)) solid #646464;
    background-color: #646464;
}

