
.dashboard-body{
    background-color: #F0F0F2;
}

.dashboard-card-title{
    font-size: 1.71vw;
    letter-spacing: 1.65px;
    font-weight: 500;
    color: #3A3C4A;
    margin-bottom: 1.14vw;
    margin-left: 0.98vw;
}

.dashboard-body .card{
    box-shadow: 0px 4px 4px #0000004D;
    border-radius: 0.7rem;
    border: 1px solid #707070;
}

.dashboard-body .card-content{
    padding: 1.66vw 1vw 0.5vw 2.8vw;
    height: 23.25vw;
    overflow-y: auto;
}

.dashboard-body .card-title{
    font-size: 1.71vw;
    color: #151B49;
    letter-spacing: 0;
    font-weight: 500;
    margin-bottom: unset;
}

.dashboard-body .card .section-day{
    font-size: 1.4vw;
    color: #3A3C4A;
    font-weight: 500;
}

.dashboard-body .card .section-intro{
    color: #3A3C4A;
    font-size: 1.3vw;
}

.dashboard-body .card .inner-card {
    background-color: #F0F0F2;
    padding: 1.5vw;
    border-radius: 0.7rem;
    color: #0A0E2A;
    font-size: 1.2vw;
    margin-bottom: 2vw;
}

.section-intro button{
    background-color: #9999995E;
    color: #3A3C4A;
    border: none;
    margin: 0.5vw 0 0 1vw;
    font-size: 1vw;
    padding: 0.5vw 1.5vw;
    cursor: pointer;
}

.dashboard-body .status{
    color: #F74B42;
    font-size: 1.1vw;
    margin-left: 2.2vw;
}

